html {
  background-color: var(--bodyBackgroundColor);
  font-family: Roboto, sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--bodyBackgroundColor);
  position: relative;
}

.projects-page {
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  padding: 2rem 4rem;
  display: flex;
}

.projectCard {
  background-color: var(--CardColor);
  color: var(--mainTextColor);
  backdrop-filter: blur(2px);
  border-radius: 10px;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 1.5rem;
  width: 100%;
  padding: 1.5rem;
  display: flex;
}

.projectCard h3 {
  font-size: 1.5rem;
}

.projectCard p {
  color: gray;
  margin-top: 3rem;
  font-size: medium;
}

.projectCreate {
  backdrop-filter: blur(2px);
  z-index: 2000;
  color: var(--mainTextColor);
  background-color: var(--logoColor);
  opacity: .5;
  border: none;
  border-radius: 10px;
  padding: 1.5rem 2rem;
  transition: all .5s;
  position: fixed;
  bottom: 0;
  right: 0;
}

.projectCreate:hover {
  cursor: pointer;
  opacity: 1;
}

.ButtonContainer {
  flex-direction: column;
  justify-content: space-between;
  gap: 1rem;
  display: flex;
}

.AddTaskButton, .deleteBtn {
  background-color: var(--logoColor);
  color: var(--mainTextColor);
  border: none;
  border-radius: 10px;
  padding: 1rem 2rem;
}

.tasksContainer {
  background-color: var(--CardColor);
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  width: 100%;
  padding: 2rem;
  display: grid;
}

.taskCard {
  background-color: var(--bodyBackgroundColor);
  border-radius: 10px;
  justify-content: space-between;
  padding: 1rem;
  display: flex;
}

.taskCard p {
  margin-top: .5rem;
}

.IconsContainer {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.completed {
  color: gray;
  text-decoration: line-through;
}
